.home {
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    position: fixed;
    color: #ffffff;
    font-size: 80px;
    padding: 20px;
    font-family: 'Press Start 2P', cursive;
    z-index: 10;
}

.choices {
    height: inherit;
    width: inherit;
}

.choice {
    position: relative;
    width: 50%;
    height: 100%;
    border: none;
    color: #ffffff;
    /* font-size: 50px;
    font-family: 'Press Start 2P', cursive;
    font-weight: 800; */
    transition: all ease-in 2s;
}

.triangle {
    background-color: #F05454;
    background-image: url(/chaos-game/static/media/Triangle.70e9ead3.png);
    background-repeat: no-repeat;
    background-position: center;
    /* transform: scale(1.2); */

    transition: 0.5s;

}

.hexagon {
    background-color: #30475E;
    background-image: url(/chaos-game/static/media/Hexagon.de6e3b57.png);
    background-repeat: no-repeat;
    background-position: center;

    transition: 0.5s;

}

.triangle:hover {
    background-size: 60%;
    z-index: 1;
    box-shadow: 10px 10px 30px rgb(3, 3, 3);
}

.hexagon:hover {
    background-size: 60%;
    box-shadow: -10px -10px 30px rgb(3, 3, 3);
}
.Triangle {

    background: #222831;
    font-family: 'Press Start 2P', cursive;
    text-align: center;
}

.controlpanel {
    position: fixed;
    top: 0;
    height: 8rem;
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-family: 'Press Start 2P', cursive;

}

.startButton {
    height: 24rem;
    width: 24rem;
    position: fixed;
    top: 50%;
    left: 50%;
    margin: -12rem 0 0 -12rem;
    font-size: 3rem;
    color: white;
    border-radius: 50%;
    background-color: #F05454;
    border: none;
    box-shadow: 20px 20px 0 0 #d94242;
    font-family: 'Press Start 2P', cursive;
}

.startButton:hover {
    background-color: #d94242;
    box-shadow: 20px 20px 0 0 #b13131;
}

.startButton:active {
    -webkit-transform: translate(15px, 15px);
            transform: translate(15px, 15px);
    box-shadow: 5px 5px 0 0 #b13131;
}


button {
    height: 100%;
}

.homeButton {
    text-align: left;
    font-size: 4rem;
    font-weight: 900;
    background-color: #F05454;
    border-radius: 0 0 100% 0;
    width: 8rem;
    padding: 0 0 2rem 3rem;
}

.clearButton {
    /* text-align: center; */
    border: none;
    font-size: 4rem;
    font-weight: 900;
    background-color: #F05454;
    border-radius: 0 0 0 100%;
    width: 8rem;
    padding: 0 0 2rem 3rem;
}

.homeButton:hover,
.clearButton:hover {
    background-color: #f43d3d;
    color: white;
    box-shadow: 0 0 10px 5px black;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    font-size: 3.3rem;
    transition: 100ms;
}

